.container {
  display: flex;
  justify-content: center;
}
ul {
  background-color: darkgrey;
  width: 500px;
  padding: 60px;
  padding-left: 70px;
  padding-right: 70px;
  padding-top: 30;
  align-content: center;
}

.list ul li a {
  padding: 50px;
  display: block;
}
/* .list li a:hover {
  width: 40%;
  background-color: darkgrey;
  padding: 20px;
} */
.list li {
  list-style-type: none;
  padding: 10px;
  width: 100%;
  background: #fff;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
  transform: transform 0.5s;
}
.list li:hover {
  transform: scale(1.1);
  z-index: 100;
  background: #ff8c00;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  color: #fff;
}
.list li span {
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  background: #ff8c00;
  color: #fff;
  display: inline-block;
  border-radius: 50%;
  margin-right: 10px;
  font-size: 12px;
  font-weight: 600;
  transform: translateY(-20px);
}
.list li:hover span {
  background: #fff;
  color: #ff8c00;
}
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.sticky + .content {
  padding-top: 102px;
}

footer {
  /* background-color: blue; */
  height: 60px;
  padding-top: 20px;
  padding-bottom: 20px;
  position: absolute;
  width: 100%;
  text-align: center;
}
